/* stylelint-disable-next-line value-keyword-case */
/** Bulma: Variables **/
/** Main **/
body {
  animation: none;
}

#dashboard {
  animation: fadeInOpacity 0.5s;
}

.menu-label {
  color: #b5b5b5;
}

.menu-list a {
  color: #1f89e5;
  border: 1px solid transparent;
}
.menu-list a.is-active {
  color: white;
  background: #1f89e5;
  border-color: #1f89e5;
}
.menu-list a:not(.is-active):hover {
  color: #1f89e5;
  background: none;
  border-color: #1f89e5;
}
.menu-list a[disabled] {
  color: #7a7a7a;
  pointer-events: none;
}
.menu-list a.is-loading::after {
  animation: spinAround 0.5s infinite linear;
  border: 2px solid whitesmoke;
  border-radius: 290486px;
  border-right-color: transparent;
  border-top-color: transparent;
  content: "";
  display: block;
  height: 1em;
  width: 1em;
  right: calc(0% + (1em / 2));
  top: calc(50% - (1em / 2));
  position: absolute !important;
}
.menu-list a.is-active[data-action=collapse] {
  color: #1f89e5 !important;
  background: none !important;
  border-color: transparent !important;
}
.menu-list a.is-active[data-action=collapse]:hover {
  border-color: #1f89e5 !important;
}
.menu-list li ul {
  border-left: 0;
  padding-left: 0;
}
.menu-list li ul li {
  border-left: 2px solid #b5b5b5;
  padding-left: 0.75em;
}
.menu-list #albumsContainer li {
  animation: fadeInOpacity 0.5s;
}

#page.fade-in {
  animation: fadeInOpacity 0.5s;
}

.pagination {
  margin-bottom: 1.25rem;
}

.pagination-previous:not([disabled]),
.pagination-next:not([disabled]),
.pagination-link:not([disabled]) {
  background-color: black;
}
.pagination-previous:not([disabled]):hover,
.pagination-next:not([disabled]):hover,
.pagination-link:not([disabled]):hover {
  background-color: whitesmoke;
}
.pagination-previous[disabled],
.pagination-next[disabled],
.pagination-link[disabled] {
  opacity: 1;
}

.pagination-link.is-current {
  color: black;
  background-color: whitesmoke;
  border-color: whitesmoke;
}
.pagination-link.is-current.is-loading::after {
  border-bottom-color: #4a4a4a;
  border-left-color: #4a4a4a;
}

li[data-action=page-ellipsis] {
  cursor: pointer;
}

.label {
  color: #ededed;
}

#page {
  /* fix overflow issue with flex */
  min-width: 0;
}

.is-linethrough {
  text-decoration: line-through;
}

#dashboard.is-loading *[data-action],
#dashboard.is-loading #menu .menu-list a {
  cursor: progress;
}
#dashboard.is-loading *[data-action$=-help],
#dashboard.is-loading .table *[data-action],
#dashboard.is-loading .column.bulk-operations *[data-action],
#dashboard.is-loading .column.exclusive-operations *[data-action] {
  cursor: pointer;
}

#statistics tr *:nth-child(1) {
  width: 50%;
}

.expirydate {
  color: #ededed;
}

.table .originalname {
  max-width: 200px;
  text-overflow: ellipsis;
  overflow: hidden;
}
.table th[title],
.table td[title] {
  cursor: help;
}

/** Thumbs extension **/
.image-container .checkbox {
  position: absolute;
  top: 11px;
  left: 11px;
}
.image-container .controls {
  display: flex;
  position: absolute;
  top: 11px;
  right: 11px;
}
.image-container .controls .button {
  border-radius: 0;
}
.image-container .controls .button:not(:active):not(:hover) {
  color: white;
  background-color: black;
}

.no-touch .image-container .checkbox {
  opacity: 0.5;
}
.no-touch .image-container .controls,
.no-touch .image-container .details {
  opacity: 0;
}
.no-touch .image-container:hover .checkbox,
.no-touch .image-container:hover .controls,
.no-touch .image-container:hover .details {
  opacity: 1;
}